
.h1{
    color: red;
}
.header {
    margin: auto;
    height: 90px;
    display:flex;
    justify-content:  space-between; 
    align-items: center;
    flex-wrap: wrap;
    background-color: var(--light-blue);
    padding:0 2em;
    box-shadow: 1px 1px 10px #666;
  }
  .logo img{
      width: 70px;
      height: 70px;
      border-radius: 50%;
      object-position: center;
      object-fit: cover;
  }
  .header nav {
    width: 90em;
    max-width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    list-style: none;
  }
  
  .header nav .header-nav {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
  }
  .header nav .header-nav > li {
      margin-left: 20px;
  }
  
  .header nav .header-nav li a {
    text-decoration: none;
    color: var(--gray);
    font-weight: 600;
    font-style: normal;
    font-size: 24px;
  }
  .header nav .header-nav li:hover {
      transform: Scale(1.05);
  
  
  }
  .header nav .header-nav li a:hover {
    color: red;
    transform: Scale(1.03);
    border-bottom: 4px solid red;
  
  }